body {
  font-family: "Roboto", Arial, sans-serif;
  background-color: #f5f5f5;
  color: #1f3a93;
  padding: 20px;
  margin: 0;
}

h1 {
  color: #fff;
  font-size: 2.5em;
  margin-bottom: 20px;
  text-align: center;
  background-color: #1f3a93;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

form {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
}

label {
  display: block;
  font-size: 1.2em;
  margin-bottom: 8px;
}

input[type="date"] {
  width: calc(100% - 22px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
}

button {
  background-color: #1f3a93;
  color: #fff;
  padding: 12px 24px;
  font-size: 1.2em;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px;
}

button:hover {
  background-color: #142662;
}

.results {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.results p,
.results ul {
  font-size: 1.2em;
  color: #1f3a93;
}

.results ul {
  list-style-type: none;
  padding: 0;
}

.results li {
  margin-bottom: 10px;
}

.results li::before {
  content: "•";
  color: #1f3a93;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

/* Ajout de couleurs différentes pour chaque type de jour */
.results p:nth-child(1) {
  color: #1e90ff; /* Bleu - jours calendaires */
}

.results p:nth-child(2) {
  color: #32cd32; /* Vert - jours ouvrables */
}

.results p:nth-child(3) {
  color: #ffa500; /* Orange - jours ouvrés */
}

.results p:nth-child(4) {
  color: #ff4500; /* Rouge - jours fériés */
}
